___CSS_LOADER_EXPORT___.push([module.id, `/* 
 * 聊天组件 Flexbox 布局样式
 * 使用 Flexbox 实现灵活的内部布局，与 Grid 布局配合使用
 */

/* 导入变量 */

/* 消息容器 Flexbox 布局 */
.assistant-message-wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.user-message .assistant-message-wrapper {
  align-items: flex-end;
}

/* 重新生成按钮容器 Flexbox 布局 */
.regenerate-container {
  display: flex;
  justify-content: flex-start;
  width: 100%;
  z-index: 1;
}

.user-message .regenerate-container {
  justify-content: flex-end;
}

/* 输入区域 Flexbox 布局 */
.chat-input {
  display: flex;
  padding: 0;
  gap: var(--spacing-md);
  align-items: center;
}

.chat-input input {
  flex: 1;
  padding: var(--spacing-sm) var(--spacing-lg);
  border: 1px solid var(--input-border);
  border-radius: 24px;
  font-size: var(--font-size-md);
  outline: none;
  transition: all var(--transition-normal) ease;
  height: 40px;
  box-sizing: border-box;
  background-color: var(--input-bg);
  color: var(--text-color);
}

.chat-input input:focus {
  border-color: var(--primary-color);
  box-shadow: 0 0 0 2px rgba(123, 85, 239, 0.25);
}

/* 聊天操作按钮 Flexbox 布局 */
.chat-action-button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  padding: 0;
  background-color: var(--primary-color);
  color: #ffffff;
  border: none;
  border-radius: var(--border-radius-full);
  cursor: pointer;
  font-size: 20px;
  line-height: 40px;
  transition: all var(--transition-normal) ease;
  flex-shrink: 0;
  box-shadow: var(--shadow-md);
  margin-top: 0;
}

.chat-action-button:hover:not(:disabled) {
  background-color: var(--primary-hover);
}

.chat-action-button:disabled {
  background-color: var(--text-secondary);
  cursor: not-allowed;
}

.chat-action-button svg {
  width: 20px;
  height: 20px;
  line-height: 1;
  fill: #ffffff;
  stroke: #ffffff;
}

.chat-action-button-text {
  color: #ffffff;
  font-weight: bold;
}

/* 强制设置按钮内所有文本为白色 */
.chat-action-button > *,
.chat-action-button > span {
  color: #ffffff;
}

/* 输入指示器 Flexbox 布局 */
.typing-indicator {
  display: flex;
  align-items: center;
  padding: 12px 16px;
}

.typing-indicator span {
  height: 8px;
  width: 8px;
  background-color: var(--typing-indicator-color);
  border-radius: var(--border-radius-full);
  display: inline-block;
  margin: 0 2px;
  animation: typing 1s infinite;
}

.typing-indicator span:nth-child(2) {
  animation-delay: 0.2s;
}

.typing-indicator span:nth-child(3) {
  animation-delay: 0.4s;
}

/* 快捷操作按钮 Flexbox 布局 */
.quick-action-button {
  padding: var(--spacing-xs) var(--spacing-sm);
  background-color: var(--quick-action-bg);
  border: 1px solid var(--quick-action-border);
  border-radius: 16px;
  color: var(--text-color);
  font-size: var(--font-size-md);
  cursor: pointer;
  transition: all var(--transition-normal) ease;
  white-space: nowrap;
  flex-shrink: 0;
  width: auto;
  min-width: fit-content;
  max-width: none;
  margin: 0;
}

.quick-action-button:hover:not(:disabled) {
  background-color: var(--quick-action-hover-bg);
  border-color: var(--primary-color);
  color: var(--primary-color);
  box-shadow: 0 2px 4px rgba(123, 85, 239, 0.2);
}

.quick-action-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  background-color: var(--quick-action-bg);
}

/* 问题面板 Flexbox 布局 */
.tab-button {
  padding: var(--spacing-sm) var(--spacing-md);
  cursor: pointer;
  background-color: transparent;
  border: none;
  border-bottom: 2px solid transparent;
  color: var(--text-secondary);
  font-size: var(--font-size-md);
  white-space: nowrap;
  transition: color var(--transition-normal), border-color var(--transition-normal);
}

.tab-button.active {
  color: var(--primary-color);
  font-weight: 600;
}

.tab-button:hover {
  color: var(--primary-color);
  background-color: transparent;
}

.question-group {
  margin-bottom: var(--spacing-md);
}

.category-heading {
  font-size: var(--font-size-md);
  font-weight: 600;
  color: var(--text-color);
  margin-bottom: var(--spacing-sm);
  margin-top: 0;
}

.questions-wrapper {
  display: flex;
  flex-wrap: wrap;
  gap: var(--spacing-sm);
}

.question-text {
  background-color: var(--quick-action-bg);
  border-radius: 12px;
  padding: var(--spacing-sm) var(--spacing-md);
  font-size: var(--font-size-sm);
  cursor: pointer;
  color: var(--text-color);
  line-height: 1.4;
  margin: 0;
  transition: background-color var(--transition-normal);
}

.question-text:hover {
  background-color: var(--quick-action-hover-bg);
}

/* 重新生成按钮 Flexbox 布局 */
.regenerate-button.quick-action-button {
  padding: var(--spacing-xs) var(--spacing-sm);
  background-color: var(--quick-action-bg);
  border: 1px solid var(--quick-action-border);
  border-radius: 16px;
  color: var(--text-color);
  font-size: 12px;
  cursor: pointer;
  transition: all var(--transition-normal) ease;
  white-space: nowrap;
  flex-shrink: 0;
  width: auto;
  min-width: fit-content;
  max-width: none;
  margin: 0;
  display: flex;
  align-items: center;
  gap: var(--spacing-xs);
}

.regenerate-button.quick-action-button:hover:not(:disabled) {
  background-color: var(--quick-action-hover-bg);
  border-color: var(--primary-color);
  color: var(--primary-color);
  box-shadow: 0 2px 4px rgba(123, 85, 239, 0.2);
}

.regenerate-button.quick-action-button svg {
  width: 14px;
  height: 14px;
}

.regenerate-button.quick-action-button span {
  font-size: 12px;
  line-height: 1;
}

/* 移动端 Flexbox 布局优化 */
@media (max-width: 768px) {
  .chat-input {
    flex-shrink: 0;
    margin-top: var(--spacing-xs);
  }
  
  .chat-input input {
    font-size: var(--font-size-sm);
    height: 36px;
    padding: var(--spacing-sm) var(--spacing-md);
    min-height: 36px;
  }
  
  .chat-action-button {
    width: 36px;
    height: 36px;
    min-width: 36px;
    min-height: 36px;
  }
  
  .tab-button {
    padding: 6px 10px;
    font-size: var(--font-size-sm);
    min-height: 32px;
  }
  
  .question-text {
    font-size: var(--font-size-sm);
    padding: var(--spacing-xs) var(--spacing-sm);
    min-height: 24px;
  }
  
  .regenerate-button.quick-action-button {
    font-size: 12px;
    padding: var(--spacing-xs) var(--spacing-sm);
  }
  
  .regenerate-button.quick-action-button svg {
    width: 14px;
    height: 14px;
  }
  
  .regenerate-button.quick-action-button span {
    font-size: 12px;
  }
  
  /* 键盘可见时的特殊处理 */
  .keyboard-visible .chat-input {
    transform: translateZ(0);
    -webkit-transform: translateZ(0);
    transition: all var(--transition-normal) ease;
    position: relative;
    z-index: 1000;
  }
}

/* 暗色主题 Flexbox 布局调整 */
@media (prefers-color-scheme: dark) {
