rection: column;\n  align-items: flex-start;\n}\n\n.user-message .assistant-message-wrapper {\n  align-items: flex-end;\n}\n\n/* 重新生成按钮容器 Flexbox 布局 */\n.regenerate-container {\n  display: flex;\n  justify-content: flex-start;\n  width: 100%;\n  z-index: 1;\n}\n\n.user-message .regenerate-container {\n  justify-content: flex-end;\n}\n\n/* 输入区域 Flexbox 布局 */\n.chat-input {\n  display: flex;\n  padding: 0;\n  gap: var(--spacing-md);\n  align-items: center;\n}\n\n.chat-input input {\n  flex: 1;\n  padding: var(--spacing-sm) var(--spacing-lg);\n  border: 1px solid var(--input-border);\n  border-radius: 24px;\n  font-size: var(--font-size-md);\n  outline: none;\n  transition: all var(--transition-normal) ease;\n  height: 40px;\n  box-sizing: border-box;\n  background-color: var(--input-bg);\n  color: var(--text-color);\n}\n\n.chat-input input:focus {\n  border-color: var(--primary-color);\n  box-shadow: 0 0 0 2px rgba(123, 85, 239, 0.25);\n}\n\n/* 聊天操作按钮 Flexbox 布局 */\n.chat-action-button {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  width: 40px;\n  height: 40px;\n  padding: 0;\n  background-color: var(--primary-color);\n  color: #ffffff;\n  border: none;\n  border-radius: var(--border-radius-full);\n  cursor: pointer;\n  font-size: 20px;\n  line-height: 40px;\n  transition: all var(--transition-normal) ease;\n  flex-shrink: 0;\n  box-shadow: var(--shadow-md);\n  margin-top: 0;\n}\n\n.chat-action-button:hover:not(:disabled) {\n  background-color: var(--primary-hover);\n}\n\n.chat-action-button:disabled {\n  background-color: var(--text-secondary);\n  cursor: not-allowed;\n}\n\n.chat-action-button svg {\n  width: 20px;\n  height: 20px;\n  line-height: 1;\n  fill: #ffffff;\n  stroke: #ffffff;\n}\n\n.chat-action-button-text {\n  color: #ffffff;\n  font-weight: bold;\n}\n\n/* 强制设置按钮内所有文本为白色 */\n.chat-action-button > *,\n.chat-action-button > span {\n  color: #ffffff;\n}\n\n/* 输入指示器 Flexbox 布局 */\n.typing-indicator {\n  display: flex;\n  align-items: center;\n  padding: 12px 16px;\n}\n\n.typing-indicator span {\n  height: 8px;\n  width: 8px;\n  background-color: var(--typing-indicator-color);\n  border-radius: var(--border-radius-full);\n  display: inline-block;\n  margin: 0 2px;\n  animation: typing 1s infinite;\n}\n\n.typing-indicator span:nth-child(2) {\n  animation-delay: 0.2s;\n}\n\n.typing-indicator span:nth-child(3) {\n  animation-delay: 0.4s;\n}\n\n/* 快捷操作按钮 Flexbox 布局 */\n.quick-action-button {\n  padding: var(--spacing-xs) var(--spacing-sm);\n  background-color: var(--quick-action-bg);\n  border: 1px solid var(--quick-action-border);\n  border-radius: 16px;\n  color: var(--text-color);\n  font-size: var(--font-size-md);\n  cursor: pointer;\n  transition: all var(--transition-normal) ease;\n  white-space: nowrap;\n  flex-shrink: 0;\n  width: auto;\n  min-width: fit-content;\n  max-width: none;\n  margin: 0;\n}\n\n.quick-action-button:hover:not(:disabled) {\n  background-color: var(--quick-action-hover-bg);\n  border-color: var(--primary-color);\n  color: var(--primary-color);\n  box-shadow: 0 2px 4px rgba(123, 85, 239, 0.2);\n}\n\n.quick-action-button:disabled {\n  opacity: 0.6;\n  cursor: not-allowed;\n  background-color: var(--quick-action-bg);\n}\n\n/* 问题面板 Flexbox 布局 */\n.tab-button {\n  padding: var(--spacing-sm) var(--spacing-md);\n  cursor: pointer;\n  background-color: transparent;\n  border: none;\n  border-bottom: 2px solid transparent;\n  color: var(--text-secondary);\n  font-size: var(--font-size-md);\n  white-space: nowrap;\n  transition: color var(--transition-normal), border-color var(--transition-normal);\n}\n\n.tab-button.active {\n  color: var(--primary-color);\n  font-weight: 600;\n}\n\n.tab-button:hover {\n  color: var(--primary-color);\n  background-color: transparent;\n}\n\n.question-group {\n  margin-bottom: var(--spacing-md);\n}\n\n.category-heading {\n  font-size: var(--font-size-md);\n  font-weight: 600;\n  color: var(--text-color);\n  margin-bottom: var(--spacing-sm);\n  margin-top: 0;\n}\n\n.questions-wrapper {\n  display: flex;\n  flex-wrap: wrap;\n  gap: var(--spacing-sm);\n}\n\n.question-text {\n  background-color: var(--quick-action-bg);\n  border-radius: 12px;\n  padding: var(--spacing-sm) var(--spacing-md);\n  font-size: var(--font-size-sm);\n  cursor: pointer;\n  color: var(--text-color);\n  line-height: 1.4;\n  margin: 0;\n  transition: background-color var(--transition-normal);\n}\n\n.question-text:hover {\n  background-color: var(--quick-action-hover-bg);\n}\n\n/* 重新生成按钮 Flexbox 布局 */\n.regenerate-button.quick-action-button {\n  padding: var(--spacing-xs) var(--spacing-sm);\n  background-color: var(--quick-action-bg);\n  border: 1px solid var(--quick-action-border);\n  border-radius: 16px;\n  color: var(--text-color);\n  font-size: 12px;\n  cursor: pointer;\n  transition: all var(--transition-normal) ease;\n  white-space: nowrap;\n  flex-shrink: 0;\n  width: auto;\n  min-width: fit-content;\n  max-width: none;\n  margin: 0;\n  display: flex;\n  align-items: center;\n  gap: var(--spacing-xs);\n}\n\n.regenerate-button.quick-action-button:hover:not(:disabled) {\n  background-color: var(--quick-action-hover-bg);\n  border-color: var(--primary-color);\n  color: var(--primary-color);\n  box-shadow: 0 2px 4px rgba(123, 85, 239, 0.2);\n}\n\n.regenerate-button.quick-action-button svg {\n  width: 14px;\n  height: 14px;\n}\n\n.regenerate-button.quick-action-button span {\n  font-size: 12px;\n  line-height: 1;\n}\n\n/* 移动端 Flexbox 布局优化 */\n@media (max-width: 768px) {\n  .chat-input {\n    flex-shrink: 0;\n    margin-top: var(--spacing-xs);\n  }\n  \n  .chat-input input {\n    font-size: var(--font-size-sm);\n    height: 36px;\n    padding: var(--spacing-sm) var(--spacing-md);\n    min-height: 36px;\n  }\n  \n  .chat-action-button {\n    width: 36px;\n    height: 36px;\n    min-width: 36px;\n    min-height: 36px;\n  }\n  \n  .tab-button {\n    padding: 6px 10px;\n    font-size: var(--font-size-sm);\n    min-height: 32px;\n  }\n  \n  .question-text {\n    font-size: var(--font-size-sm);\n    padding: var(--spacing-xs) var(--spacing-sm);\n    min-height: 24px;\n  }\n  \n  .regenerate-button.quick-action-button {\n    font-size: 12px;\n    padding: var(--spacing-xs) var(--spacing-sm);\n  }\n  \n  .regenerate-button.quick-action-button svg {\n    width: 14px;\n    height: 14px;\n  }\n  \n  .regenerate-button.quick-action-button span {\n    font-size: 12px;\n  }\n  \n  /* 键盘可见时的特殊处理 */\n  .keyboard-visible .chat-input {\n    transform: translateZ(0);\n    -webkit-transform: translateZ(0);\n    transition: all var(--transition-normal) ease;\n    position: relative;\n    z-index: 1000;\n  }\n}\n\n/* 暗色主题 Flexbox 布局调整 */\n@media (prefers-color-scheme: dark) {\n  .chat-input input {\n    background-color: var(--input-bg);\n    border-color: var(--input-border);\n    color: var(--text-color);\n  }\n  \n  .chat-action-button {\n    background-color: var(--primary-color);\n    color: #ffffff;\n  }\n  \n  .chat-action-button svg {\n    fill: #ffffff;\n    stroke: #ffffff;\n    color: #ffffff;\n  }\n  \n  .chat-action-button-text {\n    color: #ffffff;\n    font-weight: bold;\n  }\n  \n  /* 强制设置按钮内所有文本为白色 */\n  .chat-action-button > *,\n  .chat-action-button > span {\n    color: #ffffff;\n  }\n  \n  .chat-action-button:hover:not(:disabled) {\n    background-color: var(--primary-hover);\n  }\n  \n  .chat-action-button:disabled {\n    background-color: var(--text-secondary);\n    color: var(--text-secondary);\n  }\n  \n  .chat-action-button:disabled svg {\n    fill: var(--text-secondary);\n    stroke: var(--text-secondary);\n    color: var(--text-secondary);\n  }\n  \n  .typing-indicator span {\n    background-color: var(--typing-indicator-color);\n  }\n  \n  .quick-action-button {\n    background-color: var(--quick-action-bg);\n    border-color: var(--quick-action-border);\n    color: var(--text-color);\n  }\n  \n  .quick-action-button:hover:not(:disabled) {\n    background-color: var(--quick-action-hover-bg);\n    border-color: var(--primary-color);\n    color: var(--primary-color);\n  }\n  \n  .quick-action-button:disabled {\n    background-color: var(--input-bg);\n    color: var(--text-secondary);\n  }\n  \n  .tab-button {\n    color: var(--text-secondary);\n  }\n  \n  .tab-button.active,\n  .tab-button:hover {\n    color: var(--primary-color);\n  }\n  \n  .category-heading {\n    color: var(--text-color);\n    font-size: var(--font-size-md);\n  }\n  \n  .question-text {\n    background-color: var(--quick-action-bg);\n    color: var(--text-color);\n  }\n  \n  .question-text:hover {\n    background-color: var(--quick-action-hover-bg);\n  }\n  \n  .regenerate-button.quick-action-button {\n    background-color: var(--quick-action-bg);\n    border-color: var(--quick-action-border);\n    color: var(--text-color);\n  }\n  \n  .regenerate-button.quick-action-button:hover:not(:disabled) {\n    background-color: var(--quick-action-hover-bg);\n    border-color: var(--primary-color);\n    color: var(--primary-color);\n  }\n  \n  /* 移动端暗色主题 Flexbox 布局 */\n  @media (max-width: 768px) {\n    .chat-input input {\n      background-color: var(--input-bg);\n      border-color: var(--input-border);\n      color: var(--text-color);\n    }\n    \n    .chat-action-button {\n      background-color: var(--primary-color);\n      color: #ffffff;\n    }\n    \n    .chat-action-button svg {\n      fill: #ffffff;\n      stroke: #ffffff;\n      color: #ffffff;\n    }\n    \n    .chat-action-button-text {\n      color: #ffffff;\n      font-weight: bold;\n    }\n    \n    .chat-action-button:hover:not(:disabled) {\n      background-color: var(--primary-hover);\n    }\n    \n    .chat-action-button:disabled {\n      background-color: var(--text-secondary);\n      color: var(--text-secondary);\n    }\n    \n    .chat-action-button:disabled svg {\n      fill: var(--text-secondary);\n      stroke: var(--text-secondary);\n      color: var(--text-secondary);\n    }\n  }\n}"],"sourceRoot":""}]);
