___CSS_LOADER_EXPORT___.push([module.id, `/* 星盘基础样式 */

.iztro-astrolabe {
  font-family: -apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Helvetica Neue,Arial,Noto Sans,sans-serif,Apple Color Emoji,Segoe UI Emoji,Segoe UI Symbol,Noto Color Emoji;
  display: grid;
  position: relative;
  width: 100%;
  grid-gap: 3px;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: 1fr;
  max-width: 800px;
  margin: 0 auto;
  box-sizing: border-box;
  grid-template-areas:
    "g3 g4 g5 g6"
    "g2 ct ct g7"
    "g1 ct ct g8"
    "g0 g11 g10 g9";
}

/* 移动端响应式布局 */
@media (max-width: 768px) {
  .iztro-astrolabe {
    grid-template-columns: repeat(4, 1fr);
    grid-template-areas:
      "g3 g4 g5 g6"
      "g2 ct ct g7"
      "g1 ct ct g8"
      "g0 g11 g10 g9";
    grid-auto-rows: minmax(50px, auto);
    grid-gap: 1px;
    width: 100%;
    min-width: 280px;
    max-width: 500px;
    padding: 0;
    margin: 0 auto;
  }
}

/* 超小屏幕设备 */
@media (max-width: 480px) {
  .iztro-astrolabe {
    grid-template-columns: repeat(4, 1fr);
    grid-template-areas:
      "g3 g4 g5 g6"
      "g2 ct ct g7"
      "g1 ct ct g8"
      "g0 g11 g10 g9";
    grid-auto-rows: minmax(45px, auto);
    grid-gap: 1px;
    width: 100%;
    min-width: 260px;
    max-width: 100%;
    padding: 0;
    margin: 0 auto;
  }
}

.iztro-star-mutagen {
  font-weight: normal;
  font-size: var(--iztro-star-font-size-small);
  border-radius: var(--border-radius-sm);
  color: #fff;
  display: inline-block;
  margin-left: 1px;
  padding: 0 2px;
}

.star-with-mutagen {
  position: relative;
}

.star-with-mutagen::before {
  bottom: 0;
  content: " ";
  left: -4px;
  position: absolute;
  top: 0;
  width: 4px;
  transition: all 0.25s ease-in-out;
}

.star-with-mutagen::after {
  content: " ";
  position: absolute;
  left: 0;
  bottom: -4px;
  right: 0;
  height: 4px;
  transition: all 0.25s ease-in-out;
}

/* 移动端星体样式优化 */
@media (max-width: 768px) {
  .iztro-star-mutagen {
    font-size: var(--iztro-star-font-size-small);
    border-radius: var(--border-radius-xs);
    padding: 0 1px;
  }
  
  .star-with-mutagen::before {
    width: 2px;
    left: -2px;
  }
  
  .star-with-mutagen::after {
    height: 2px;
    bottom: -2px;
  }
}`, "",{"version":3,"sources":["webpack://./src/styles/components/iztrolabe.css"],"names":[],"mappings":"AAAA,WAAW;;AAEX;EACE,yKAAyK;EACzK,aAAa;EACb,kBAAkB;EAClB,WAAW;EACX,aAAa;EACb,qCAAqC;EACrC,mBAAmB;EACnB,gBAAgB;EAChB,cAAc;EACd,sBAAsB;EACtB;;;;mBAIiB;AACnB;;AAEA,aAAa;AACb;EACE;IACE,qCAAqC;IACrC;;;;qBAIiB;IACjB,kCAAkC;IAClC,aAAa;IACb,WAAW;IACX,gBAAgB;IAChB,gBAAgB;IAChB,UAAU;IACV,cAAc;EAChB;AACF;;AAEA,WAAW;AACX;EACE;IACE,qCAAqC;IACrC;;;;qBAIiB;IACjB,kCAAkC;IAClC,aAAa;IACb,WAAW;IACX,gBAAgB;IAChB,eAAe;IACf,UAAU;IACV,cAAc;EAChB;AACF;;AAEA;EACE,mBAAmB;EACnB,4CAA4C;EAC5C,sCAAsC;EACtC,WAAW;EACX,qBAAqB;EACrB,gBAAgB;EAChB,cAAc;AAChB;;AAEA;EACE,kBAAkB;AACpB;;AAEA;EACE,SAAS;EACT,YAAY;EACZ,UAAU;EACV,kBAAkB;EAClB,MAAM;EACN,UAAU;EACV,iCAAiC;AACnC;;AAEA;EACE,YAAY;EACZ,kBAAkB;EAClB,OAAO;EACP,YAAY;EACZ,QAAQ;EACR,WAAW;EACX,iCAAiC;AACnC;;AAEA,cAAc;AACd;EACE;IACE,4CAA4C;IAC5C,sCAAsC;IACtC,cAAc;EAChB;;EAEA;IACE,UAAU;IACV,UAAU;EACZ;;EAEA;IACE,WAAW;IACX,YAAY;EACd;AACF","sourcesContent":["/* 星盘基础样式 */\n\n.iztro-astrolabe {\n  font-family: -apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Helvetica Neue,Arial,Noto Sans,sans-serif,Apple Color Emoji,Segoe UI Emoji,Segoe UI Symbol,Noto Color Emoji;\n  display: grid;\n  position: relative;\n  width: 100%;\n  grid-gap: 3px;\n  grid-template-columns: repeat(4, 1fr);\n  grid-auto-rows: 1fr;\n  max-width: 800px;\n  margin: 0 auto;\n  box-sizing: border-box;\n  grid-template-areas:\n    \"g3 g4 g5 g6\"\n    \"g2 ct ct g7\"\n    \"g1 ct ct g8\"\n    \"g0 g11 g10 g9\";\n}\n\n/* 移动端响应式布局 */\n@media (max-width: 768px) {\n  .iztro-astrolabe {\n    grid-template-columns: repeat(4, 1fr);\n    grid-template-areas:\n      \"g3 g4 g5 g6\"\n      \"g2 ct ct g7\"\n      \"g1 ct ct g8\"\n      \"g0 g11 g10 g9\";\n    grid-auto-rows: minmax(50px, auto);\n    grid-gap: 1px;\n    width: 100%;\n    min-width: 280px;\n    max-width: 500px;\n    padding: 0;\n    margin: 0 auto;\n  }\n}\n\n/* 超小屏幕设备 */\n@media (max-width: 480px) {\n  .iztro-astrolabe {\n    grid-templ