.astrolabe-content::-webkit-scrollbar {
  width: 0px;
  background: transparent;
}

.synastry-container::-webkit-scrollbar {
  width: 0px;
  background: transparent;
}

/* Firefox隐藏滚动条 */
.astrolabe-content {
  scrollbar-width: none;
  -ms-overflow-style: none;
}

.synastry-container {
  scrollbar-width: none;
  -ms-overflow-style: none;
}

/* 加载指示器样式 */
.loading-indicator {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: var(--spacing-xl);
  font-size: var(--font-size-md);
  color: var(--gray-600);
  background-color: var(--gray-100);
  border-radius: var(--border-radius-md);
  margin: var(--spacing-lg) 0;
}

.loading-indicator::after {
  content: '';
  width: 20px;
  height: 20px;
  margin-left: var(--spacing-sm);
  border: 2px solid var(--gray-300);
  border-top-color: var(--primary-color);
  border-radius: var(--border-radius-full);
  animation: spin 1s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

/* 后台AI生成指示器样式 */
.background-ai-indicator {
  position: absolute;
  top: 10px;
  right: 10px;
  background-color: rgba(0, 123, 255, 0.9);
  color: white;
  padding: 6px 12px;
  border-radius: 20px;
  font-size: 12px;
  display: flex;
  align-items: center;
  gap: 6px;
  box-shadow: 0 2px 8px rgba(0, 123, 255, 0.3);
  z-index: 10;
  animation: pulse 2s infinite;
}

.indicator-content {
  display: flex;
  align-items: center;
  gap: 6px;
}

.indicator-text {
  font-size: 12px;
  white-space: nowrap;
}

/* 后台AI指示器中的打字动画 */
.background-ai-indicator .typing-indicator {
  display: flex;
  align-items: center;
}

.background-ai-indicator .typing-indicator span {
  height: 6px;
  width: 6px;
  background-color: white;
  border-radius: var(--border-radius-full);
  display: inline-block;
  margin: 0 1px;
  animation: typing 1.4s infinite;
}

.background-ai-indicator .typing-indicator span:nth-child(2) {
  animation-delay: 0.2s;
}

.background-ai-indicator .typing-indicator span:nth-child(3) {
  animation-delay: 0.4s;
}

@keyframes typing {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-3px);
  }
}

@keyframes pulse {
  0% {
    box-shadow: 0 2px 8px rgba(0, 123, 255, 0.3);
  }
  50% {
    box-shadow: 0 2px 12px rgba(0, 123, 255, 0.5);
  }
  100% {
    box-shadow: 0 2px 8px rgba(0, 123, 255, 0.3);
  }
}

/* 移动端后台AI指示器样式 */
@media (max-width: 768px) {
  .background-ai-indicator {
    top: 5px;
    right: 5px;
    padding: 4px 8px;
  }
  
  .indicator-text {
    font-size: 10px;
  }
  
  .background-ai-indicator .typing-indicator span {
    height: 4px;
    width: 4px;
  }
