CT,WAAW;EACX,wCAAwC;EACxC,YAAY;EACZ,iBAAiB;EACjB,mBAAmB;EACnB,eAAe;EACf,aAAa;EACb,mBAAmB;EACnB,QAAQ;EACR,4CAA4C;EAC5C,WAAW;EACX,4BAA4B;AAC9B;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,QAAQ;AACV;;AAEA;EACE,eAAe;EACf,mBAAmB;AACrB;;AAEA,kBAAkB;AAClB;EACE,aAAa;EACb,mBAAmB;AACrB;;AAEA;EACE,WAAW;EACX,UAAU;EACV,uBAAuB;EACvB,wCAAwC;EACxC,qBAAqB;EACrB,aAAa;EACb,+BAA+B;AACjC;;AAEA;EACE,qBAAqB;AACvB;;AAEA;EACE,qBAAqB;AACvB;;AAEA;EACE;IACE,wBAAwB;EAC1B;EACA;IACE,2BAA2B;EAC7B;AACF;;AAEA;EACE;IACE,4CAA4C;EAC9C;EACA;IACE,6CAA6C;EAC/C;EACA;IACE,4CAA4C;EAC9C;AACF;;AAEA,iBAAiB;AACjB;EACE;IACE,QAAQ;IACR,UAAU;IACV,gBAAgB;EAClB;;EAEA;IACE,eAAe;EACjB;;EAEA;IACE,WAAW;IACX,UAAU;EACZ;AACF","sourcesContent":["/* 布局样式 */\n\n/* 全局样式 */\nbody {\n  margin: 0;\n  padding: 0;\n  box-sizing: border-box;\n  background-color: var(--gray-100);\n  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;\n  -webkit-font-smoothing: antialiased;\n  -moz-osx-font-smoothing: grayscale;\n}\n\n/* 移动端body样式 */\n@media (max-width: 768px) {\n  body {\n    background-color: var(--white);\n    overflow-x: hidden;\n    position: fixed;\n    width: 100%;\n    height: 100%;\n  }\n}\n\n.App {\n  max-width: 1200px;\n  margin: 0 auto;\n  padding: var(--spacing-lg);\n  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;\n}\n\n/* 移动端App容器样式 */\n@media (max-width: 768px) {\n  .App {\n    padding: 0;\n    max-width: 100%;\n    width: 100%;\n    height: 100vh;\n    height: 100dvh;\n    display: flex;\n    flex-direction: column;\n    overflow: hidden;\n    position: relative;\n    /* 使用统一的安全区域适配 */\n    padding-top: env(safe-area-inset-top);\n    padding-bottom: env(safe-area-inset-bottom);\n  }\n}\n\n.content {\n  display: flex;\n  flex-direction: column;\n  height: 100%;\n}\n\n/* 移动端内容样式 */\n@media (max-width: 768px) {\n  .content {\n    flex-direction: column;\n    height: 100%;\n    overflow: hidden;\n  }\n}\n\n/* 表单区域样式 */\n.form-section {\n  background: var(--gray-100);\n  padding: var(--spacing-xl);\n  border-radius: var(--border-radius-md);\n  box-shadow: var(--box-shadow-md);\n  width: 600px;\n  margin: 0 auto;\n}\n\n/* 移动端表单区域样式 */\n@media (max-width: 768px) {\n  .form-section {\n    width: 100%;\n    padding: var(--spacing-md);\n    border-radius: 0;\n    box-shadow: none;\n    background-color: var(--white);\n    height: 100vh;\n    display: flex;\n    flex-direction: column;\n    justify-content: flex-start;\n    overflow-y: auto;\n    -webkit-overflow-scrolling: touch;\n    box-sizing: border-box;\n    position: relative;\n  }\n  \n  /* 合盘表单区域优化 */\n  .form-section form {\n    max-width: 100%;\n    overflow: visible;\n    box-sizing: border-box;\n    padding-bottom: var(--spacing-xl);\n  }\n  \n  /* 合盘表单分组优化 */\n  .form-section h3 {\n    font-size: var(--font-size-lg);\n    margin: var(--spacing-lg) 0 var(--spacing-md);\n    padding-bottom: var(--spacing-sm);\n    border-bottom: 1px solid var(--gray-300);\n    color: var(--gray-800);\n    font-weight: 600;\n  }\n  \n  /* 第一个分组不添加上边距 */\n  .form-section h3:first-of-type {\n    margin-top: var(--spacing-sm);\n  }\n}\n\n/* 星盘区域样式 */\n.astrolabe-section {\n  background: var(--gray-100);\n  padding: var(--spacing-lg);\n  border-radius: var(--border-radius-md);\n  box-shadow: var(--box-shadow-md);\n}\n\n.astrolabe-header {\n  text-align: center;\n  margin-bottom: var(--spacing-lg);\n}\n\n.astrolabe-header h2 {\n  color: var(--gray-800);\n  margin-bottom: var(--spacing-sm);\n}\n\n.astrolabe-header p {\n  color: var(--gray-600);\n  margin: 0;\n}\n\n.astrolabe-container {\n  overflow: auto;\n  height: 100%;\n  box-sizing: border-box;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n}\n\n.rechart-button-container {\n  display: flex;\n  justify-content: center;\n  margin-top: var(--spacing-lg);\n  width: 100%;\n}\n\n/* AI聊天区域样式 */\n.ai-chat-section {\n  margin-top: var(--spacing-xl);\n}\n\n/* 结果区域样式 */\n.result-section {\n  display: flex;\n  flex-direction: column;\n  flex-grow: 1;\n  background: var(--white);\n  padding: var(--spacing-lg);\n  border-radius: var(--border-radius-md);\n  box-shadow: var(--box-shadow-lg);\n  height: calc(100vh - 80px);\n}\n\n/* 移动端result-section样式 */\n@media (max-width: 768px) {\n  .result-section {\n    border-radius: 0;\n    box-shadow: none;\n    padding: 0;\n    height: 100vh;\n    margin: 0;\n    width: 100%;\n    overflow: hidden;\n    display: flex;\n    flex-direction: column;\n    box-sizing: border-box;\n    background-color: var(--white);\n  }\n  \n  .tab-navigation {\n    padding: var(--spacing-sm);\n    background-color: var(--white);\n    border-bottom: 1px solid var(--gray-300);\n    width: 100%;\n    box-sizing: border-box;\n    margin: 0;\n    position: sticky;\n    top: env(safe-area-inset-top);\n    z-index: 10;\n  }\n  \n  .tab-content {\n    flex: 1;\n    overflow: hidden;\n    height: calc(100vh - 44px);\n    padding: 0;\n    margin: 0;\n    width: 100%;\n    box-sizing: border-box;\n  }\n}\n\n/* 标签导航样式 */\n.tab-navigation {\n  display: flex;\n  justify-content: center;\n  margin-bottom: var(--spacing-lg);\n}\n\n.tab-content {\n  flex-grow: 1;\n  display: flex;\n  flex-direction: column;\n  min-height: 0;\n  overflow: hidden;\n}\n\n/* 星盘内容样式 */\n.astrolabe-content {\n  height: 100%;\n  overflow: hidden;\n}\n\n/* 移动端星盘内容样式 */\n@media (max-width: 768px) {\n  .astrolabe-content {\n    height: 100%;\n    overflow: auto;\n    -webkit-overflow-scrolling: touch;\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    justify-content: flex-start;\n    padding: var(--spacing-sm) 0;\n    width: 100%;\n  }\n  \n  .astrolabe-container {\n    width: 100%;\n    max-width: 100%;\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    padding: 0 var(--spacing-sm);\n    box-sizing: border-box;\n  }\n  \n  .iztro-astrolabe {\n    min-width: 280px;\n    max-width: 100%;\n    margin: 0 auto;\n    transform-origin: center;\n  }\n  \n .rechart-button-container {\n   margin-top: var(--spacing-md);\n   margin-bottom: var(--spacing-md);\n   width: 100%;\n   padding: 0 var(--spacing-md);\n   box-sizing: border-box;\n   /* 使用统一的安全区域适配 */\n   padding-bottom: max(var(--spacing-md), env(safe-area-inset-bottom));\n }\n  \n  .rechart-button {\n    width: 100%;\n    padding: var(--spacing-sm);\n    font-size: var(--font-size-md);\n  }\n}\n\n/* 超小屏幕设备 */\n@media (max-width: 480px) {\n  .astrolabe-content {\n    padding: 0;\n  }\n  \n  .astrolabe-container {\n    padding: 0 var(--spacing-xs);\n  }\n  \n  .iztro-astrolabe {\n    min-width: 260px;\n    max-width: 100%;\n  }\n  \n  .rechart-button-container {\n    padding: 0 var(--spacing-sm);\n  }\n}\n\n/* AI内容样式 */\n.ai-content {\n  flex-grow: 1;\n  display: flex;\n  flex-direction: column;\n  overflow: hidden;\n  min-height: 0;\n}\n\n@media (max-width: 768px) {\n  .tab-content {\n    min-height: 300px;\n    overflow: hidden;\n  }\n  \n  .ai-content {\n    flex-grow: 1;\n    overflow: hidden;\n    padding: 0;\n    margin: 0;\n    width: 100%;\n    box-sizing: border-box;\n  }\n}\n\n/* 合盘模式样式 */\n.synastry-container {\n  display: flex;\n  flex-direction: row;\n  justify-content: space-around;\n  align-items: flex-start;\n  width: 100%;\n  gap: var(--spacing-lg);\n  flex-wrap: wrap;\n}\n\n.synastry-person {\n  flex: 1;\n  min-width: 300px;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n}\n\n.synastry-person h3 {\n  margin-bottom: var(--spacing-md);\n  color: var(--gray-800);\n  text-align: center;\n}\n\n/* 在小屏幕上垂直排列 */\n@media (max-width: 768px) {\n  .synastry-container {\n    flex-direction: column;\n    align-items: center;\n    width: 100%;\n    height: 100%;\n    overflow: auto;\n    -webkit-overflow-scrolling: touch;\n    padding: var(--spacing-sm);\n    box-sizing: border-box;\n  }\n  \n  .synastry-person {\n    width: 100%;\n    margin-bottom: var(--spacing-md);\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    background-color: var(--white);\n    border-radius: var(--border-radius-md);\n    padding: var(--spacing-md);\n    box-shadow: var(--box-shadow-sm);\n    box-sizing: border-box;\n  }\n  \n  .synastry-person:last-child {\n    margin-bottom: 0;\n  }\n  \n  .synastry-person h3 {\n    margin-bottom: var(--spacing-sm);\n    font-size: var(--font-size-md);\n    color: var(--gray-800);\n    font-weight: 600;\n    text-align: center;\n    width: 100%;\n    padding-bottom: var(--spacing-sm);\n    border-bottom: 1px solid var(--gray-200);\n  }\n}\n\n/* 隐藏垂直滚动条但保持滚动功能 */\n.astrolabe-content::-webkit-scrollbar {\n  width: 0px;\n  background: transparent;\n}\n\n.synastry-container::-webkit-scrollbar {\n  width: 0px;\n  background: transparent;\n}\n\n/* Firefox隐藏滚动条 */\n.astrolabe-content {\n  scrollbar-width: none;\n  -ms-overflow-style: none;\n}\n\n.synastry-container {\n  scrollbar-width: none;\n  -ms-overflow-style: none;\n}\n\n/* 加载指示器样式 */\n.loading-indicator {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  padding: var(--spacing-xl);\n  font-size: var(--font-size-md);\n  color: var(--gray-600);\n  background-color: var(--gray-100);\n  border-radius: var(--border-radius-md);\n  margin: var(--spacing-lg) 0;\n}\n\n.loading-indicator::after {\n  content: '';\n  width: 20px;\n  height: 20px;\n  margin-left: var(--spacing-sm);\n  border: 2px solid var(--gray-300);\n  border-top-color: var(--primary-color);\n  border-radius: var(--border-radius-full);\n  animation: spin 1s linear infinite;\n}\n\n@keyframes spin {\n  to {\n    transform: rotate(360deg);\n  }\n}\n\n/* 后台AI生成指示器样式 */\n.background-ai-indicator {\n  position: absolute;\n  top: 10px;\n  right: 10px;\n  background-color: rgba(0, 123, 255, 0.9);\n  color: white;\n  padding: 6px 12px;\n  border-radius: 20px;\n  font-size: 12px;\n  display: flex;\n  align-items: center;\n  gap: 6px;\n  box-shadow: 0 2px 8px rgba(0, 123, 255, 0.3);\n  z-index: 10;\n  animation: pulse 2s infinite;\n}\n\n.indicator-content {\n  display: flex;\n  align-items: center;\n  gap: 6px;\n}\n\n.indicator-text {\n  font-size: 12px;\n  white-space: nowrap;\n}\n\n/* 后台AI指示器中的打字动画 */\n.background-ai-indicator .typing-indicator {\n  display: flex;\n  align-items: center;\n}\n\n.background-ai-indicator .typing-indicator span {\n  height: 6px;\n  width: 6px;\n  background-color: white;\n  border-radius: var(--border-radius-full);\n  display: inline-block;\n  margin: 0 1px;\n  animation: typing 1.4s infinite;\n}\n\n.background-ai-indicator .typing-indicator span:nth-child(2) {\n  animation-delay: 0.2s;\n}\n\n.background-ai-indicator .typing-indicator span:nth-child(3) {\n  animation-delay: 0.4s;\n}\n\n@keyframes typing {\n  0%, 100% {\n    transform: translateY(0);\n  }\n  50% {\n    transform: translateY(-3px);\n  }\n}\n\n@keyframes pulse {\n  0% {\n    box-shadow: 0 2px 8px rgba(0, 123, 255, 0.3);\n  }\n  50% {\n    box-shadow: 0 2px 12px rgba(0, 123, 255, 0.5);\n  }\n  100% {\n    box-shadow: 0 2px 8px rgba(0, 123, 255, 0.3);\n  }\n}\n\n/* 移动端后台AI指示器样式 */\n@media (max-width: 768px) {\n  .background-ai-indicator {\n    top: 5px;\n    right: 5px;\n    padding: 4px 8px;\n  }\n  \n  .indicator-text {\n    font-size: 10px;\n  }\n  \n  .background-ai-indicator .typing-indicator span {\n    height: 4px;\n    width: 4px;\n  }\n}"],"sourceRoot":""}]);
