  @media (max-width: 768px) {
    .mobile-bottom-fixed {
      padding-bottom: max(var(--spacing-sm, 8px), var(--safe-area-inset-bottom));
    }
    
    .keyboard-visible .mobile-bottom-fixed {
      padding-bottom: max(var(--spacing-sm, 8px), var(--safe-area-inset-bottom));
    }
  }
}

