.android-device .mobile-bottom-fixed {
  /* 安卓设备上增加额外的底部内边距，避免被小白条遮挡 */
  padding-bottom: max(var(--spacing-sm, 8px), calc(var(--safe-area-inset-bottom) + var(--android-navigation-bar-height)));
}

