  box-sizing: border-box;
}

/* 聊天相关组件的安全区域适配 */
.chat-container-safe-area {
  @media (max-width: 768px) {
    height: 100vh;
    height: 100dvh;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    /* 确保顶部有足够的空间，避免被刘海区域遮挡 */
    padding-top: env(safe-area-inset-top);
  }
}

