.iztro-center-palace {
  grid-area: ct;
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
}

/* 移动端响应式布局 */
@media (max-width: 768px) {
  .iztro-center-palace {
    overflow: auto;
    max-height: 250px;
    padding: var(--spacing-xs);
  }
  
  .iztro-center-palace ul.basic-info {
    grid-template-columns: 1fr;
    column-gap: var(--spacing-sm);
    margin: var(--spacing-sm);
  }
  
  .horo-buttons {
    flex-wrap: wrap;
    gap: var(--spacing-xs);
    margin: var(--spacing-sm);
  }
  
  .horo-buttons .center-button {
    padding: var(--spacing-xs);
    font-size: var(--iztro-star-font-size-small);
    min-height: 32px;
  }
}

/* 超小屏幕设备 */
@media (max-width: 480px) {
  .iztro-center-palace {
    max-height: 200px;
    padding: var(--spacing-xs);
  }
  
  .horo-buttons .center-button {
    padding: var(--spacing-xs);
    font-size: var(--iztro-star-font-size-small);
    min-height: 30px;
  }
}

.iztro-center-palace-centralize {
  text-align: center;
}

.iztro-center-palace ul.basic-info {
  margin: var(--spacing-md);
  padding: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: var(--spacing-lg);
  font-size: var(--iztro-star-font-size-small);
}

.iztro-center-palace ul.basic-info li {
  list-style: none;
  margin-bottom: var(--spacing-xs);
}

.iztro-center-palace .center-title {
  padding: var(--spacing-sm) 0;
  margin: 0;
  font-size: var(--iztro-star-font-size-big);
  font-weight: 600;
  text-align: center;
  border-bottom: 1px dashed var(--iztro-color-border);
}

.horo-buttons {
  margin: var(--spacing-md);
  font-size: var(--iztro-star-font-size-small);
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.horo-buttons .center-button {
  display: block;
  text-align: center;
  padding: var(--spacing-sm);
  border: 1px solid var(--iztro-color-border);
  cursor: pointer;
  transition: all 0.25s ease-in-out;
  color: var(--iztro-color-text);
  user-select: none;
  border-radius: var(--border-radius-sm);
  min-height: 36px;
  -webkit-tap-highlight-color: transparent;
}

.horo-buttons .center-button:not(.disabled):hover {
  color: var(--iztro-color-major);
  background-color: var(--iztro-color-border);
}

.horo-buttons .center-button.disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.horo-buttons .center-horo-hour {
  display: flex;
  align-items: center;
  font-size: var(--iztro-star-font-size-small);
}

/* 移除了 iztro-copyright 样式 */

#palace-line {
  stroke: var(--iztro-color-awesome);
  opacity: 0.6;
  transition: all 0.25s ease-in-out;
}

#palace-line.decadal {
  stroke: var(--iztro-color-decadal);
}

.solar-horoscope {
  display: flex;
  align-items: center;
  gap: var(--spacing-sm);
  margin: var(--spacing-sm) 0;
}

.solar-horoscope-centralize {
  justify-content: center;
}

.solar-horoscope .today {
  display: inline-block;
  font-size: var(--iztro-star-font-size-small);
  cursor: pointer;
  border: 1px solid var(--iztro-color-border);
  padding: 0 var(--spacing-sm);
  transition: all 0.25s ease-in-out;
  border-radius: var(--border-radius-sm);
  -webkit-tap-highlight-color: transparent;
}

.solar-horoscope .today:hover {
  color: var(--iztro-color-major);
  background-color: var(--iztro-color-border);
}

/* 移动端太阳历样式优化 */
@media (max-width: 768px) {
  .solar-horoscope {
    gap: var(--spacing-xs);
    margin: var(--spacing-xs) 0;
  }
  
  .solar-horoscope .today {
    padding: 0 var(--spacing-xs);
    font-size: var(--iztro-star-font-size-small);
  }
}`, "",{"version":3,"sources":["webpack://./src/styles/components/izpalace-center.css"],"names":[],"mappings":"AAAA,WAAW;;AAEX;EACE,aAAa;EACb,kBAAkB;EAClB,WAAW;EACX,YAAY;EACZ,aAAa;EACb,sBAAsB;EACtB,mBAAmB;EACnB,uBAAuB;EACvB,sBAAsB;AACxB;;AAEA,aAAa;AACb;EACE;IACE,cAAc;IACd,iBAAiB;IACjB,0BAA0B;EAC5B;;EAEA;IACE,0BAA0B;IAC1B,6BAA6B;IAC7B,yBAAyB;EAC3B;;EAEA;IACE,eAAe;IACf,sBAAsB;IACtB,yBAAyB;EAC3B;;EAEA;IACE,0BAA0B;IAC1B,4CAA4C;IAC5C,gBAAgB;EAClB;AACF;;AAEA,WAAW;AACX;EACE;IACE,iBAAiB;IACjB,0BAA0B;EAC5B;;EAEA;IACE,0BAA0B;IAC1B,4CAA4C;IAC5C,gBAAgB;EAClB;AACF;;AAEA;EACE,kBAAkB;AACpB;;AAEA;EACE,yBAAyB;EACzB,UAAU;EACV,aAAa;EACb,qCAAqC;EACrC,6BAA6B;EAC7B,4CAA4C;AAC9C;;AAEA;EACE,gBAAgB;EAChB,gCAAgC;AAClC;;AAEA;EACE,4BAA4B;EAC5B,SAAS;EACT,0CAA0C;EAC1C,gBAAgB;EAChB,kBAAkB;EAClB,mDAAmD;AACrD;;AAEA;EACE,yBAAyB;EACzB,4CAA4C;EAC5C,aAAa;EACb,6BAA6B;EAC7B,eAAe;AACjB;;AAEA;EACE,cAAc;EACd,kBAAkB;EAClB,0BAA0B;EAC1B,2CAA2C;EAC3C,eAAe;EACf,iCAAiC;EACjC,8BAA8B;EAC9B,iBAAiB;EACjB,sCAAsC;EACtC,gBAAgB;EAChB,wCAAwC;AAC1C;;AAEA;EACE,+BAA+B;EAC/B,2CAA2C;AAC7C;;AAEA;EACE,YAAY;EACZ,mBAAmB;AACrB;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,4CAA4C;AAC9C;;AAEA,2BAA2B;;AAE3B;EACE,kCAAkC;EAClC,YAAY;EACZ,iCAAiC;AACnC;;AAEA;EACE,kCAAkC;AACpC;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,sBAAsB;EACtB,2BAA2B;AAC7B;;AAEA;EACE,uBAAuB;AACzB;;AAEA;EACE,qBAAqB;EACrB,4CAA4C;EAC5C,eAAe;EACf,2CAA2C;EAC3C,4BAA4B;EAC5B,iCAAiC;EACjC,sCAAsC;EACtC,wCAAwC;AAC1C;;AAEA;EACE,+BAA+B;EAC/B,2CAA2C;AAC7C;;AAEA,eAAe;AACf;EACE;IACE,sBAAsB;IACtB,2BAA2B;EAC7B;;EAEA;IACE,4BAA4B;IAC5B,4CAA4C;EAC9C;AACF","sourcesContent":["/* 中心宫位样式 */\n\n.iztro-center-palace {\n  grid-area: ct;\n  position: relative;\n  width: 100%;\n  height: 100%;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  box-sizing: border-box;\n}\n\n/* 移动端响应式布局 */\n@media (max-width: 768px) {\n  .iztro-center-palace {\n    overflow: auto;\n    max-height: 250px;\n    padding: var(--spacing-xs);\n  }\n  \n  .iztro-center-palace ul.basic-info {\n  