___CSS_LOADER_EXPORT___.push([module.id, `.iztro-center-palace {
  grid-area: ct;
  position: relative;
  width: 100%;
  height: 100%;
}

/* 移动端响应式布局 */
@media (max-width: 768px) {
  .iztro-center-palace {
    overflow: auto;
    max-height: 300px;
  }
  
  .iztro-center-palace ul.basic-info {
    grid-template-columns: 1fr;
    column-gap: 10px;
  }
  
  .horo-buttons {
    flex-wrap: wrap;
    gap: 5px;
  }
  
  .horo-buttons .center-button {
    padding: 3px;
    font-size: 10px;
  }
}

/* 超小屏幕设备 */
@media (max-width: 480px) {
  .iztro-center-palace {
    max-height: 250px;
  }
  
  .horo-buttons .center-button {
    padding: 2px;
    font-size: 9px;
  }
}
.iztro-center-palace-centralize {
  text-align: center;
}
.iztro-center-palace ul.basic-info {
  margin: 10px;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 20px;
}
.iztro-center-palace ul.basic-info li {
  list-style: none;
}
.iztro-center-palace .center-title {
  padding: 5px 0;
  margin: 0;
  font-size: var(--iztro-star-font-size-big);
  font-weight: bold;
  text-align: center;
  border-bottom: 1px dashed var(--iztro-color-border);
}

.horo-buttons {
  margin: 10px;
  font-size: var(--iztro-star-font-size-small);
  display: flex;
  justify-content: space-around;
}
.horo-buttons .center-button {
  display: block;
  text-align: center;
  padding: 5px;
  border: 1px solid var(--iztro-color-border);
  cursor: pointer;
  transition: all 0.25s ease-in-out;
  color: var(--iztro-color-text);
  user-select: none;
}
.horo-buttons .center-button:not(.disabled):hover {
  color: var(--iztro-color-major);
  background-color: var(--iztro-color-border);
}
.horo-buttons .center-button.disabled {
  opacity: 0.5;
  cursor: not-allowed;
}
.horo-buttons .center-horo-hour {
  display: flex;
  align-items: center;
}

/* 移除了 iztro-copyright 样式 */

#palace-line {
  stroke: var(--iztro-color-awesome);
  opacity: 0.6;
  transition: all 0.25s ease-in-out;
}
#palace-line.decadal {
  stroke: var(--iztro-color-decadal);
}

.solar-horoscope {
  display: flex;
  align-items: center;
  gap: 10px;
}

.solar-horoscope-centralize {
  justify-content: center;
}

.solar-horoscope .today {
  display: inline-block;
  font-size: var(--iztro-star-font-size-small);
  cursor: pointer;
  border: 1px solid var(--iztro-color-border);
  padding: 0 5px;
  transition: all 0.25s ease-in-out;
}

.solar-horoscope .today:hover {
  color: var(--iztro-color-major);
  background-color: var(--iztro-color-border);
}`, "",{"version":3,"sources":["webpack://./src/IzpalaceCenter/IzpalaceCenter.css"],"names":[],"mappings":"AAAA;EACE,aAAa;EACb,kBAAkB;EAClB,WAAW;EACX,YAAY;AACd;;AAEA,aAAa;AACb;EACE;IACE,cAAc;IACd,iBAAiB;EACnB;;EAEA;IACE,0BAA0B;IAC1B,gBAAgB;EAClB;;EAEA;IACE,eAAe;IACf,QAAQ;EACV;;EAEA;IACE,YAAY;IACZ,eAAe;EACjB;AACF;;AAEA,WAAW;AACX;EACE;IACE,iBAAiB;EACnB;;EAEA;IACE,YAAY;IACZ,cAAc;EAChB;AACF;AACA;EACE,kBAAkB;AACpB;AACA;EACE,YAAY;EACZ,UAAU;EACV,aAAa;EACb,qCAAqC;EACrC,gBAAgB;AAClB;AACA;EACE,gBAAgB;AAClB;AACA;EACE,cAAc;EACd,SAAS;EACT,0CAA0C;EAC1C,iBAAiB;EACjB,kBAAkB;EAClB,mDAAmD;AACrD;;AAEA;EACE,YAAY;EACZ,4CAA4C;EAC5C,aAAa;EACb,6BAA6B;AAC/B;AACA;EACE,cAAc;EACd,kBAAkB;EAClB,YAAY;EACZ,2CAA2C;EAC3C,eAAe;EACf,iCAAiC;EACjC,8BAA8B;EAC9B,iBAAiB;AACnB;AACA;EACE,+BAA+B;EAC/B,2CAA2C;AAC7C;AACA;EACE,YAAY;EACZ,mBAAmB;AACrB;AACA;EACE,aAAa;EACb,mBAAmB;AACrB;;AAEA,2BAA2B;;AAE3B;EACE,kCAAkC;EAClC,YAAY;EACZ,iCAAiC;AACnC;AACA;EACE,kCAAkC;AACpC;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,SAAS;AACX;;AAEA;EACE,uBAAuB;AACzB;;AAEA;EACE,qBAAqB;EACrB,4CAA4C;EAC5C,eAAe;EACf,2CAA2C;EAC3C,cAAc;EACd,iCAAiC;AACnC;;AAEA;EACE,+BAA+B;EAC/B,2CAA2C;AAC7C","sourcesContent":[".iztro-center-palace {\n  grid-area: ct;\n  position: relative;\n  width: 100%;\n  height: 100%;\n}\n\n/* 移动端响应式布局 */\n@media (max-width: 768px) {\n  .iztro-center-palace {\n    overflow: auto;\n    max-height: 300px;\n  }\n  \n  .iztro-center-palace ul.basic-info {\n    grid-template-columns: 1fr;\n    column-gap: 10px;\n  }\n  \n  .horo-buttons {\n    flex-wrap: wrap;\n    gap: 5px;\n  }\n  \n  .horo-buttons .center-button {\n    padding: 3px;\n    font-size: 10px;\n  }\n}\n\n/* 超小屏幕设备 */\n@media (max-width: 480px) {\n  .iztro-center-palace {\n    max-height: 250px;\n  }\n  \n  .horo-buttons .center-button {\n    padding: 2px;\n    font-size: 9px;\n  }\n}\n.iztro-center-palace-centralize {\n  text-align: center;\n}\n.iztro-center-palace ul.basic-info {\n  margin: 10px;\n  padding: 0;\n  display: grid;\n  grid-template-columns: repeat(2, 1fr);\n  