/* 导入变量 */

/* 移动端聊天容器样式 */
@media (max-width: 768px) {
  .ai-chat {
    border-radius: 0;
    height: 100vh;
    height: 100dvh;
    display: grid;
    grid-template-rows:
      var(--header-height)
      minmax(0, 1fr)
      auto;
    overflow: hidden;
    width: 100%;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    
    /* 确保 Grid 容器正确分配空间 */
    grid-auto-rows: minmax(min-content, max-content);
    
    /* 添加顶部安全区域，避免被刘海区域遮挡 */
    padding-top: var(--safe-area-inset-top);
  }
  
  .ai-chat.mobile {
    /* 移动端特定样式 */
    --mobile-header-height: 60px;
    --mobile-input-height: 90px;
  }
  
  /* 移动端消息区域样式 */
  .chat-messages {
    padding: var(--spacing-sm);
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    
    /* 使用 Grid 自动分配空间 */
    grid-row: 2;
    
    /* 使用 padding 替代 margin 来处理安全区域 */
    padding-top: calc(var(--safe-area-top) + var(--header-height));
    
    /* 移除最大高度限制，让 Grid 自动分配空间 */
    max-height: none;
    height: auto;
    min-height: 0;
    
    /* 确保内容区域可以滚动 */
    box-sizing: border-box;
    
    /* iOS PWA 模式下增加额外的顶部内边距，避免被刘海区域遮挡 */
    padding-top: calc(var(--safe-area-inset-top) + var(--header-height) + 10px);
  }
  
  /* 移动端消息内容样式 */
  .message-content {
    max-width: 90%;
    font-size: var(--font-size-md);
    padding: 12px 16px;
  }
  
  /* 移动端输入区域样式 */
  .chat-input-container {
    padding: 4px;
    background-color: var(--background-color);
    border-top: 1px solid var(--border-color);
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 100%;
    box-sizing: border-box;
    
    /* 使用 Grid 自动分配空间 */
    grid-row: 3;
    
    /* 添加底部安全区域 */
    padding-bottom: max(8px, calc(var(--safe-area-bottom) + 10px));
    
    /* 确保输入区域可见且足够高 */
    min-height: 100px;
    
    /* 移除最大高度限制，让问题面板能够撑起容器 */
    max-height: none;
    
    /* 移除滚动，让容器能够根据内容扩展 */
    overflow-y: visible;
    
    /* 添加一些基本样式确保可见性 */
    z-index: 100;
    position: relative;
    
    /* 限制宽度，确保不会过宽 */
    margin: 0 auto;
    
    /* 确保容器不会超出屏幕宽度 */
    overflow-x: hidden;
    
    /* 确保容器能够根据内容自适应高度 */
    height: auto;
    
    /* 使用 flex-grow: 0 防止容器过度扩展 */
    flex-grow: 0;
    
    /* 确保容器能够根据内容调整大小 */
    align-self: flex-start;
  }
  
  /* 移动端快捷操作区域样式 */
  .quick-actions-container {
    margin-bottom: 2px;
    flex-shrink: 0;
  }
  
  .quick-actions {
    gap: 2px;
    padding-bottom: 0;
  }
  
  .quick-action-button {
    font-size: var(--font-size-md);
    padding: var(--spacing-xs) var(--spacing-sm);
    
    /* 确保按钮有足够的最小宽度 */
    min-width: 60px;
    
    /* 允许按钮在空间不足时换行 */
    flex-wrap: wrap;
  }
  
  /* 移动端问题面板样式 */
  .question-panel {
    padding: 4px;
