  /* 移动端输入框样式 */
  .chat-input {
    flex-shrink: 0;
    margin-top: 0;
  }
  
  .chat-input input {
    font-size: var(--font-size-sm);
    height: 36px;
    padding: 4px 10px;
    
    /* 确保输入框不会占据全部宽度，为按钮留出空间 */
