    /* 防止iOS Safari上输入框缩放 */
    font-size: 16px;
    transform: translateZ(0);
    -webkit-transform: translateZ(0);
  }
  
  /* 移动端发送按钮样式 */
  .chat-action-button {
    width: 36px;
    height: 36px;
    color: #ffffff;
    min-width: 36px;
    
    /* 防止按钮在键盘弹出时变形 */
    transform: translateZ(0);
