    min-height: auto;
  }
  
  /* 键盘可见时聊天消息区域的特殊处理 */
  .keyboard-visible .chat-messages {
    /* 确保在键盘弹出时聊天消息区域仍然可见 */
    height: auto;
    
    /* 键盘可见时调整 Grid 行位置 */
    grid-row: 2;
    
    /* 保持顶部边距，避免被tab-navigation遮住 */
