  font-size: 0.85em;
  line-height: 1.5;
  color: var(--text-color);
}

.markdown-content .markdown-pre {
  background-color: var(--quick-action-bg);
  padding: 0.8em;
  margin: 0.5em 0;
  border-radius: var(--border-radius-md);
  overflow-x: auto;
  font-family: 'SFMono-Regular', Consolas, 'Liberation Mono', Menlo, monospace;
  font-size: 0.85em;
  line-height: 1.5;
  color: var(--text-color);
}

