    padding: 1px;
    min-height: 45px;
  }
}

.iztro-palace.focused-palace {
  background-color: #aab8d32f;
}

.iztro-palace.opposite-palace {
  background-color: #93f73d4f;
}

.iztro-palace.surrounded-palace {
  background-color: #aff46f24;
}

.iztro-palace-major {
  grid-area: major;
  display: flex;
  flex-wrap: wrap;
  gap: 1px;
  overflow: hidden;
  font-size: var(--iztro-star-font-size-small);
}

.iztro-palace-minor {
  grid-area: minor;
  justify-self: center;
  display: flex;
  flex-wrap: wrap;
  gap: 1px;
  overflow: hidden;
  font-size: var(--iztro-star-font-size-small);
}

.iztro-palace-adj {
  grid-area: adj;
  display: inline-flex;
  justify-self: flex-end;
  gap: 1px;
  white-space: nowrap;
  text-align: right;
  overflow: hidden;
  font-size: var(--iztro-star-font-size-small);
}

