@media (max-width: 768px) {
  .iztro-palace-adj {
    white-space: normal;
    text-align: right;
    flex-wrap: wrap;
    font-size: var(--iztro-star-font-size-small);
  }
}

.iztro-palace-horo-star {
  grid-area: horo;
  align-self: center;
}

.iztro-palace-horo-star .stars {
  display: flex;
  gap: 1px;
  font-size: var(--iztro-star-font-size-small);
}

.iztro-palace-scope {
  white-space: nowrap;
  text-align: center;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: var(--iztro-star-font-size-small);
}

.iztro-palace-scope-decadal {
  font-weight: 600;
}

.iztro-palace-fate {
  grid-area: fate;
  align-self: flex-end;
  white-space: nowrap;
  justify-content: center;
  display: flex;
  gap: 1px;
  height: 15px;
  overflow: hidden;
  font-size: var(--iztro-star-font-size-small);
}

/* 移动端文本换行处理 */
@media (max-width: 768px) {
  .iztro-palace-scope {
    white-space: normal;
    text-align: center;
    word-break: break-word;
