___CSS_LOADER_EXPORT___.push([module.id, `/* 媒体查询混合器 */

/* 移动端样式 */
@media (max-width: 768px) {
  .mobile-hidden {
    display: none;
  }
  
  .mobile-full-width {
    width: 100%;
  }
  
  .mobile-flex-column {
    flex-direction: column;
  }
  
  .mobile-no-padding {
    padding: 0;
  }
  
  .mobile-no-margin {
    margin: 0;
  }
  
  .mobile-no-border-radius {
    border-radius: 0;
  }
  
  .mobile-no-box-shadow {
    box-shadow: none;
  }
  
  .mobile-text-center {
    text-align: center;
  }
  
  .mobile-bg-white {
    background-color: var(--white);
  }
  
  /* 移动端字体大小调整 */
  .mobile-font-sm {
    font-size: var(--font-size-sm);
  }
  
  .mobile-font-xs {
    font-size: var(--font-size-xs);
  }
  
  /* 移动端星盘字体大小调整 */
  .iztro-astrolabe-theme-default {
    --iztro-star-font-size-big: 11px;
    --iztro-star-font-size-small: 10px;
  }
}

/* 超小屏幕设备 */
@media (max-width: 480px) {
  .xs-hidden {
    display: none;
  }
  
  .xs-full-width {
    width: 100%;
  }
  
  .xs-flex-column {
    flex-direction: column;
  }
  
  .xs-no-padding {
    padding: 0;
  }
  
  .xs-no-margin {
    margin: 0;
  }
  
  /* 超小屏幕设备字体大小调整 */
  .iztro-astrolabe-theme-default {
    --iztro-star-font-size-big: 10px;
    --iztro-star-font-size-small: 9px;
  }
}

/* 平板设备 */
@media (min-width: 769px) and (max-width: 1024px) {
  .tablet-hidden {
    display: none;
  }
  
  .tablet-full-width {
    width: 100%;
  }
}

/* 桌面设备 */
@media (min-width: 1025px) {
  .desktop-hidden {
    display: none;
  }
}

/* 横屏模式 */
@media (orientation: landscape) and (max-width: 768px) {
  .landscape-hidden {
    display: none;
  }
}

/* 竖屏模式 */
@media (orientation: portrait) and (max-width: 768px) {
  .portrait-hidden {
    display: none;
  }
}

/* 高分辨率设备 */
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  .retina-hidden {
    display: none;
  }
}

/* 暗色模式 */
@media (prefers-color-scheme: dark) {
  .dark-hidden {
    display: none;
  }
}

/* 亮色模式 */
@media (prefers-color-scheme: light) {
  .light-hidden {
    display: none;
  }
}

/* 减少动画模式 */
@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}`, "",{"version":3,"sources":["webpack://./src/styles/mixins.css"],"names":[],"mappings":"AAAA,YAAY;;AAEZ,UAAU;AACV;EACE;IACE,aAAa;EACf;;EAEA;IACE,WAAW;EACb;;EAEA;IACE,sBAAsB;EACxB;;EAEA;IACE,UAAU;EACZ;;EAEA;IACE,SAAS;EACX;;EAEA;IACE,gBAAgB;EAClB;;EAEA;IACE,gBAAgB;EAClB;;EAEA;IACE,kBAAkB;EACpB;;EAEA;IACE,8BAA8B;EAChC;;EAEA,cAAc;EACd;IACE,8BAA8B;EAChC;;EAEA;IACE,8BAA8B;EAChC;;EAEA,gBAAgB;EAChB;IACE,gCAAgC;IAChC,kCAAkC;EACpC;AACF;;AAEA,WAAW;AACX;EACE;IACE,aAAa;EACf;;EAEA;IACE,WAAW;EACb;;EAEA;IACE,sBAAsB;EACxB;;EAEA;IACE,UAAU;EACZ;;EAEA;IACE,SAAS;EACX;;EAEA,iBAAiB;EACjB;IACE,gCAAgC;IAChC,iCAAiC;EACnC;AACF;;AAEA,SAAS;AACT;EACE;IACE,aAAa;EACf;;EAEA;IACE,WAAW;EACb;AACF;;AAEA,SAAS;AACT;EACE;IACE,aAAa;EACf;AACF;;AAEA,SAAS;AACT;EACE;IACE,aAAa;EACf;AACF;;AAEA,SAAS;AACT;EACE;IACE,aAAa;EACf;AACF;;AAEA,WAAW;AACX;EACE;IACE,aAAa;EACf;AACF;;AAEA,SAAS;AACT;EACE;IACE,aAAa;EACf;AACF;;AAEA,SAAS;AACT;EACE;IACE,aAAa;EACf;AACF;;AAEA,WAAW;AACX;EACE;IACE,qCAAqC;IACrC,uCAAuC;IACvC,sCAAsC;IACtC,gCAAgC;EAClC;AACF","sourcesContent":["/* 媒体查询混合器 */\n\n/* 移动端样式 */\n@media (max-width: 768px) {\n  .mobile-hidden {\n    display: none;\n  }\n  \n  .mobile-full-width {\n    width: 100%;\n  }\n  \n  .mobile-flex-column {\n    flex-direction: column;\n  }\n  \n  .mobile-no-padding {\n    padding: 0;\n  }\n  \n  .mobile-no-margin {\n    margin: 0;\n  }\n  \n  .mobile-no-border-radius {\n    border-radius: 0;\n  }\n  \n  .mobile-no-box-shadow {\n    box-shadow: none;\n  }\n  \n  .mobile-text-center {\n    text-align: center;\n  }\n  \n  .mobile-bg-white {\n    background-color: var(--white);\n  }\n  \n  /* 移动端字体大小调整 */\n  .mobile-font-sm {\n    font-size: var(--font-size-sm);\n  }\n  \n  .mobile-font-xs {\n    font-size: var(--font-size-xs);\n  }\n  \n  /* 移动端星盘字体大小调整 */\n  .iztro-astrolabe-theme-default {\n    --iztro-star-font-size-big: 11px;\n    --iztro-star-font-size-small: 10px;\n  }\n}\n\n/* 超小屏幕设备 */\n@media (max-width: 480px) {\n  .xs-hidden {\n    display: none;\n  }\n  \n  .xs-full-width {\n  