.form-group {
  margin-bottom: var(--spacing-lg);
}

/* 移动端表单组样式 */
@media (max-width: 768px) {
  .form-group {
    margin-bottom: var(--spacing-md);
  }
}

.form-group label {
  display: block;
  margin-bottom: var(--spacing-xs);
  font-weight: 600;
  color: var(--gray-700);
  font-size: var(--font-size-sm);
}

.form-group-segmented label {
  margin-bottom: var(--spacing-xs);
  font-weight: 600;
  color: var(--gray-700);
  font-size: var(--font-size-sm);
}

.form-group-segmented {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 0;
  /* 添加防缩放和偏移的样式 */
  touch-action: manipulation;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -webkit-tap-highlight-color: transparent;
  transform: translate3d(0, 0, 0);
  -webkit-transform: translate3d(0, 0, 0);
  /* 确保在移动端设备上正确显示 */
  will-change: transform;
  -webkit-will-change: transform;
  /* 防止在点击时出现偏移 */
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
  /* 防止整个组件在移动端点击时上移 */
  -webkit-transform: translate3d(0, 0, 0);
  -webkit-transform-style: preserve-3d;
  -webkit-perspective: 1000;
  /* 防止在移动端点击时页面滚动 */
  touch-action: manipulation;
  -webkit-touch-action: manipulation;
  /* 确保在移动端点击时不会改变位置 */
  position: relative;
  z-index: 1;
}

.form-group-segmented .segmented-control {
  margin: 0;
  width: auto;
  /* 防止在点击时出现偏移 */
  transform: translateZ(0);
  -webkit-transform: translateZ(0);
  /* 确保在移动端设备上正确显示 */
  will-change: transform;
  -webkit-will-change: transform;
  /* 防止在点击时出现偏移 */
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
  /* 防止整个组件在移动端点击时上移 */
  -webkit-transform: translate3d(0, 0, 0);
  -webkit-transform-style: preserve-3d;
  -webkit-perspective: 1000;
}

.form-group input,
.form-group select {
  width: 100%;
  padding: var(--spacing-md);
  border: 1px solid var(--gray-400);
  border-radius: var(--border-radius-sm);
  font-size: var(--font-size-md);
  box-sizing: border-box;
}

/* 移动端输入框样式优化 */
@media (max-width: 768px) {
  .form-group input,
  .form-group select {
    padding: var(--spacing-md);
    font-size: var(--font-size-md);
    border-radius: var(--border-radius-md);
    border: 1px solid var(--gray-300);
    background-color: var(--white);
    /* 触摸区域优化 */
    min-height: 44px;
    /* 防止iOS缩放 */
    touch-action: manipulation;
    /* 确保输入框在移动端有足够的间距 */
    margin-bottom: var(--spacing-xs);
    /* 减少内边距，提高紧凑度 */
    box-sizing: border-box;
    /* 增强触摸体验 */
    -webkit-tap-highlight-color: transparent;
    /* 禁用自动放大 */
    font-size: 16px;
  }
  
  .form-group input:focus,
  .form-group select:focus {
    border-color: var(--iztro-color-active);
    outline: none;
    box-shadow: 0 0 0 2px rgba(123, 85, 239, 0.2);
    background-color: var(--white);
  }
  
  /* 优化选择框在移动端的显示 */
  .form-group select {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-repeat: no-repeat;
    background-position: right 10px center;
    background-size: 16px;
    padding-right: 35px;
    /* 修复iOS设备上select字体蓝色问题 */
    color: #333333;
  }
  
  /* 优化数字输入框在移动端的体验 */
  .form-group input[type="number"] {
    -moz-appearance: textfield;
  }
  
  .form-group input[type="number"]::-webkit-outer-spin-button,
  .form-group input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }
}

.form-group input:focus,
.form-group select:focus {
  border-color: var(--primary-color);
  outline: none;
  box-shadow: 0 0 0 2px rgba(123, 85, 239, 0.25);
}

.form-group-inline {
  display: flex;
  gap: var(--spacing-lg);
  margin-bottom: var(--spacing-lg);
}

/* 移动端内联表单组样式 */
@media (max-width: 768px) {
  .form-group-inline {
    flex-direction: column;
    gap: var(--spacing-sm);
    margin-bottom: var(--spacing-md);
  }
  
  .form-group-inline .form-group {
    margin-bottom: 0;
  }
}

.form-group-inline .form-group {
  flex: 1;
  margin-bottom: 0;
}

.form-group-inline .form-group input {
  width: 100%;
}

/* 移动端保持水平排列的表单组 */
@media (max-width: 768px) {
  .form-group-inline-mobile {
    display: flex;
    flex-direction: row;
    gap: var(--spacing-sm);
    margin-bottom: var(--spacing-md);
  }
  
  .form-group-inline-mobile .form-group {
    flex: 1;
    margin-bottom: 0;
  }
  
  .form-group-inline-mobile .form-group label {
    font-size: var(--font-size-xs);
    margin-bottom: var(--spacing-xs);
  }
}

/* 移动端特定样式 */
@media (max-width: 768px) {
.form-group-segmented {
  /* 在移动端保持适当的高度 */
  min-height: 28px;
  /* 确保在移动端有足够的触摸区域 */
  padding: 4px 12px;
  /* 防止点击时的默认缩放行为 */
  touch-action: manipulation;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -webkit-tap-highlight-color: transparent;
  /* 防止在点击时出现偏移 */
  transform: translate3d(0, 0, 0);
  -webkit-transform: translate3d(0, 0, 0);
  /* 确保在移动端设备上正确显示 */
  will-change: transform;
  -webkit-will-change: transform;
  /* 防止在点击时出现偏移 */
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
  /* 防止整个组件在移动端点击时上移 */
  -webkit-transform: translate3d(0, 0, 0);
  -webkit-transform-style: preserve-3d;
  -webkit-perspective: 1000;
  /* 防止在移动端点击时页面滚动 */
  touch-action: manipulation;
  -webkit-touch-action: manipulation;
  /* 确保在移动端点击时不会改变位置 */
  position: relative;
  z-index: 1;
}
}

/* 暗色模式表单样式 */
@media (prefers-color-scheme: dark) {
  .form-group label,
  .form-group-segmented label {
    color: var(--iztro-color-text);
  }
  
  .form-group input,
  .form-group select {
    background-color: #2d2d2d;
    border-color: #495057;
    color: #ffffff;
  }
  
  .form-group input:focus,
  .form-group select:focus {
    border-color: var(--iztro-color-active);
    box-shadow: 0 0 0 2px rgba(123, 85, 239, 0.2);
  }
  
  /* 移动端暗色模式表单样式 */
  @media (max-width: 768px) {
    .form-group input,
    .form-group select {
      background-color: #2d2d2d;
      border-color: #495057;
      color: #ffffff;
      /* 确保iOS设备上select字体颜色正确 */
      color: #ffffff !important;
    }
    
    .form-group input:focus,
    .form-group select:focus {
      background-color: #2d2d2d;
    }
  }
}
