/* 旋转动画 */
@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* 脉冲动画 */
@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(0, 123, 255, 0.4);
  }
  70% {
    box-shadow: 0 0 0 10px rgba(0, 123, 255, 0);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(0, 123, 255, 0);
  }
}

/* 淡入动画 */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* 淡出动画 */
@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

/* 滑入动画 */
@keyframes slideIn {
  from {
    transform: translateY(20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

/* 滑出动画 */
@keyframes slideOut {
  from {
    transform: translateY(0);
    opacity: 1;
  }
  to {
    transform: translateY(20px);
    opacity: 0;
  }
}

/* 缩放动画 */
@keyframes scaleIn {
  from {
    transform: scale(0.8);
    opacity: 0;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}

/* 弹跳动画 */
@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-20px);
  }
  60% {
    transform: translateY(-10px);
  }
}

/* 摇摆动画 */
@keyframes shake {
  0%, 100% {
    transform: translateX(0);
  }
  10%, 30%, 50%, 70%, 90% {
    transform: translateX(-5px);
  }
  20%, 40%, 60%, 80% {
    transform: translateX(5px);
  }
}

/* 打字动画 */
@keyframes typing {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-5px);
  }
}

/* 加载动画 */
@keyframes loading {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

/* 动画工具类 */
.animate-spin {
  animation: spin 1s linear infinite;
}

.animate-pulse {
  animation: pulse 2s infinite;
}

/* 限制无限循环动画的使用，避免热更新问题 */
@media (prefers-reduced-motion: no-preference) {
  .animate-spin {
    animation: spin 1s linear infinite;
  }

  .animate-pulse {
    animation: pulse 2s infinite;
  }
}

.animate-fade-in {
  animation: fadeIn 0.5s ease-in-out;
}

.animate-fade-out {
  animation: fadeOut 0.5s ease-in-out;
}

.animate-slide-in {
  animation: slideIn 0.5s ease-in-out;
}

.animate-slide-out {
  animation: slideOut 0.5s ease-in-out;
}

.animate-scale-in {
  animation: scaleIn 0.5s ease-in-out;
}

.animate-bounce {
  animation: bounce 1s ease-in-out;
}

.animate-shake {
  animation: shake 0.5s ease-in-out;
}

.animate-typing {
  animation: typing 1s ease-in-out infinite;
}

.animate-loading {
  animation: loading 1s linear infinite;
}

/* 限制无限循环动画的使用，避免热更新问题 */
@media (prefers-reduced-motion: no-preference) {
  .animate-typing {
    animation: typing 1s ease-in-out infinite;
  }

  .animate-loading {
    animation: loading 1s linear infinite;
  }
}

/* 过渡效果 */
.transition-all {
  transition: all var(--transition-normal) ease;
}

.transition-colors {
  transition: color var(--transition-normal) ease, background-color var(--transition-normal) ease, border-color var(--transition-normal) ease;
}

.transition-opacity {
  transition: opacity var(--transition-normal) ease;
}

.transition-transform {
  transition: transform var(--transition-normal) ease;
}

.transition-width {
  transition: width var(--transition-normal) ease;
}

.transition-height {
  transition: height var(--transition-normal) ease;
}

.transition-padding {
  transition: padding var(--transition-normal) ease;
}

.transition-margin {
  transition: margin var(--transition-normal) ease;
}

.transition-border {
  transition: border-color var(--transition-normal) ease, border-width var(--transition-normal) ease;
}

.transition-box-shadow {
  transition: box-shadow var(--transition-normal) ease;
}

/* 延迟动画 */
.delay-100 {
  animation-delay: 100ms;
}

.delay-200 {
  animation-delay: 200ms;
}

.delay-300 {
  animation-delay: 300ms;
}

.delay-500 {
  animation-delay: 500ms;
}

.delay-700 {
  animation-delay: 700ms;
}

.delay-1000 {
  animation-delay: 1000ms;
}

/* 动画持续时间 */
.duration-100 {
  animation-duration: 100ms;
}

.duration-200 {
  animation-duration: 200ms;
}

.duration-300 {
  animation-duration: 300ms;
}

.duration-500 {
  animation-duration: 500ms;
}

.duration-700 {
  animation-duration: 700ms;
}

.duration-1000 {
  animation-duration: 1000ms;
}

/* 动画填充模式 */
.fill-none {
  animation-fill-mode: none;
}

.fill-forwards {
  animation-fill-mode: forwards;
}

.fill-backwards {
  animation-fill-mode: backwards;
}

.fill-both {
  animation-fill-mode: both;
}

/* 动画迭代次数 */
.iterate-1 {
  animation-iteration-count: 1;
}

.iterate-2 {
  animation-iteration-count: 2;
}

.iterate-3 {
  animation-iteration-count: 3;
}

.iterate-infinite {
  animation-iteration-count: infinite;
}

/* 动画方向 */
.direction-normal {
  animation-direction: normal;
}

.direction-reverse {
  animation-direction: reverse;
}

.direction-alternate {
  animation-direction: alternate;
}

.direction-alternate-reverse {
  animation-direction: alternate-reverse;
}

/* 动画播放状态 */
.play-running {
  animation-play-state: running;
}

.play-paused {
  animation-play-state: paused;
