___CSS_LOADER_EXPORT___.push([module.id, `/* 聊天输入区域样式 */

.chat-input {
  display: flex;
  padding: 0;
  gap: var(--spacing-md);
  align-items: center;
}

.chat-input input {
  flex: 1;
  padding: var(--spacing-sm) var(--spacing-lg);
  border: 1px solid var(--gray-400);
  border-radius: 24px;
  font-size: var(--font-size-md);
  outline: none;
  transition: all var(--transition-normal) ease;
  height: 40px;
  box-sizing: border-box;
}

.chat-input input:focus {
  border-color: var(--primary-color);
  box-shadow: none;
  /* 禁用移动端点击时的蓝色高亮 */
  -webkit-tap-highlight-color: transparent;
  -webkit-user-select: none;
  user-select: none;
}

.chat-action-button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  padding: 0;
  background-color: var(--primary-color);
  color: #ffffff;
  border: none;
  border-radius: var(--border-radius-full);
  cursor: pointer;
  font-size: 20px;
  line-height: 40px;
  transition: all var(--transition-normal) ease;
  flex-shrink: 0;
  box-shadow: 0 2px 5px rgba(0, 123, 255, 0.3);
  margin-top: 0;
  /* 禁用移动端点击时的蓝色高亮 */
  -webkit-tap-highlight-color: transparent;
  -webkit-user-select: none;
  user-select: none;
}

.chat-action-button svg {
  width: 20px;
  height: 20px;
  line-height: 1;
}

.chat-action-button:hover:not(:disabled) {
  background-color: var(--primary-hover);
}

.chat-action-button:disabled {
  background-color: var(--gray-400);
  cursor: not-allowed;
}

@media (max-width: 768px) {
  .chat-input {
    flex-shrink: 0;
    margin-top: var(--spacing-xs);
    position: relative;
    z-index: 10;
  }
  
  .chat-input input {
    font-size: var(--font-size-sm);
    height: 36px;
    padding: var(--spacing-sm) var(--spacing-md);
    min-height: 36px;
    /* 防止iOS Safari上输入框缩放 */
    font-size: 16px;
    transform: translateZ(0);
    -webkit-transform: translateZ(0);
  }
  
  .chat-action-button {
    width: 36px;
    height: 36px;
    min-width: 36px;
    min-height: 36px;
    transform: translateZ(0);
    -webkit-transform: translateZ(0);
  }
  
  /* 虚拟键盘可见时的样式 - 使用统一的安全区域适配 */
  .keyboard-visible .chat-input {
    transform: translateZ(0);
    -webkit-transform: translateZ(0);
    transition: all 0.3s ease;
    position: relative;
    z-index: 1000;
  }
}`, "",{"version":3,"sources":["webpack://./src/styles/chat/chat-input.css"],"names":[],"mappings":"AAAA,aAAa;;AAEb;EACE,aAAa;EACb,UAAU;EACV,sBAAsB;EACtB,mBAAmB;AACrB;;AAEA;EACE,OAAO;EACP,4CAA4C;EAC5C,iCAAiC;EACjC,mBAAmB;EACnB,8BAA8B;EAC9B,aAAa;EACb,6CAA6C;EAC7C,YAAY;EACZ,sBAAsB;AACxB;;AAEA;EACE,kCAAkC;EAClC,gBAAgB;EAChB,kBAAkB;EAClB,wCAAwC;EACxC,yBAAyB;EACzB,iBAAiB;AACnB;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,WAAW;EACX,YAAY;EACZ,UAAU;EACV,sCAAsC;EACtC,cAAc;EACd,YAAY;EACZ,wCAAwC;EACxC,eAAe;EACf,eAAe;EACf,iBAAiB;EACjB,6CAA6C;EAC7C,cAAc;EACd,4CAA4C;EAC5C,aAAa;EACb,kBAAkB;EAClB,wCAAwC;EACxC,yBAAyB;EACzB,iBAAiB;AACnB;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,cAAc;AAChB;;AAEA;EACE,sCAAsC;AACxC;;AAEA;EACE,iCAAiC;EACjC,mBAAmB;AACrB;;AAEA;EACE;IACE,cAAc;IACd,6BAA6B;IAC7B,kBAAkB;IAClB,WAAW;EACb;;EAEA;IACE,8BAA8B;IAC9B,YAAY;IACZ,4CAA4C;IAC5C,gBAAgB;IAChB,uBAAuB;IACvB,eAAe;IACf,wBAAwB;IACxB,gCAAgC;EAClC;;EAEA;IACE,WAAW;IACX,YAAY;IACZ,eAAe;IACf,gBAAgB;IAChB,wBAAwB;IACxB,gCAAgC;EAClC;;EAEA,6BAA6B;EAC7B;IACE,wBAAwB;IACxB,gCAAgC;IAChC,yBAAyB;IACzB,kBAAkB;IAClB,aAAa;EACf;AACF","sourcesContent":["/* 聊天输入区域样式 */\n\n.chat-input {\n  display: flex;\n  padding: 0;\n  gap: var(--spacing-md);\n  align-items: center;\n}\n\n.chat-input input {\n  flex: 1;\n  padding: var(--spacing-sm) var(--spacing-lg);\n  border: 1px solid var(--gray-400);\n  border-radius: 24px;\n  font-size: var(--font-size-md);\n  outline: none;\n  transition: all var(--transition-normal) ease;\n  height: 40px;\n  box-sizing: border-box;\n}\n\n.chat-input input:focus {\n  border-color: var(--primary-color);\n  box-shadow: none;\n  /* 禁用移动端点击时的蓝色高亮 */\n  -webkit-tap-highlight-color: transparent;\n  -webkit-user-select: none;\n  user-select: none;\n}\n\n.chat-action-button {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  width: 40px;\n  height: 40px;\n  padding: 0;\n  background-color: var(--primary-color);\n  color: #ffffff;\n  border: none;\n  border-radius: var(--border-radius-full);\n  cursor: p